.form {
	margin: 0 auto;
	&_sm {
		max-width: 460px; }
	&_md {
		max-width: 800px;
		@include lg {
			max-width: 600px; }
		.form__col {
			padding: 10px; } }
	&_block {
		max-width: 100%; }
	&__section {
		position: relative;
		margin-bottom: 25px;
		&_sm {
			margin-bottom: 10px;
			@include hsm {
				margin-bottom: 2px; } } }
	&__row {
		position: relative;
		@include clr;
		&:before {
			content: '';
			position: absolute;
			top: 0;
			left: 50%;
			bottom: 0;
			margin-left: -1px;
			border-left: 2px solid $lightGray; } }
	&__col {
		float: left;
		padding: 12px 20px;
		width: 50%;
		@include poynt {
			width: 100%; }
		&_xl {
			width: 100%; }
		&_xs {
			width: 25%;
			@include poynt {
				width: 100%; } }
		&_poynt-50 {
			@include poynt {
				width: 50%; } } }
	&__columns {
		margin: 0 -10px;
		@include clr; }
	&__column {
		float: left;
		padding: 0 10px 25px;
		width: 100%;
		&_wide {
			width: 100%; }
		&_md {
			width: 50%;
			@include poynt {
				width: 100%; } }
		&_sm {
			width: 33.33%; }
		&_xs {
			width: 25%;
			@include poynt {
				width: 100%; } } }
	&__intro {
		padding: 0 0 25px;
		opacity: 0.7; }
	&__link {
		display: inline-block;
		vertical-align: top;
		padding: 9px 0 7px;
		color: inherit;
		font-size: 14px;
		line-height: 1;
		@include hover {
			text-decoration: none; } }

	//mods for terminal
	&_terminal {
		max-width: 680px;
		@include clr; }
	&__aside {
		float: right;
		width: 30%; }
	&__body {
		width: 70%;
		float: left; }
	&__aside,
	&__body {
		padding: 0 10px; }
	.form__submit {
		min-width: 140px; }
	&__error {
		padding: 5px 0 4px;
		color: $red;
		font-size: 14px;
		line-height: 1.2; }
	&__switcher-box {
		margin-bottom: 0 !important; }
	&__hidden {
		display: none;
		&.is-open {
			display: block; } }
	&__text {
		p {
			margin: 0; } }
	&__rounding {
		padding-right: 102px;
		color: $blue;
		font-size: 14px;
		font-style: italic;
		line-height: 1.3;
		text-align: right; } }
