html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5,
h6, p, blockquote, pre, a, abbr, acronym, address, big,
cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small,
strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt,
dd, ol, ul, li, fieldset, form, label, legend, table, caption,
tbody, tfoot, thead, tr, th, td, article, aside, canvas, details,
embed, figure, figcaption, footer, header, hgroup, menu, nav,
output, ruby, section, summary, time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  vertical-align: baseline;
}

html {
  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}

img, fieldset, a img {
  border: none;
}

input[type="text"],
input[type="email"],
input[type="tel"],
textarea,
button {
  -webkit-appearance: none;
}

input[type="submit"],
button,
a {
  cursor: pointer;
  -webkit-tap-highlight-color: transparent;
}

input[type="submit"]::-moz-focus-inner,
button::-moz-focus-inner,
a::-moz-focus-inner {
  padding: 0;
  border: 0;
}

textarea {
  overflow: auto;
}

input, button {
  margin: 0;
  padding: 0;
  border: 0;
}

div, input, textarea, select, button,
h1, h2, h3, h4, h5, h6, a, span, a:focus {
  outline: none;
}

ul, ol, li {
  list-style-type: none;
}

table {
  border-spacing: 0;
  border-collapse: collapse;
  width: 100%;
}

html {
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
  -webkit-tap-highlight-color: transparent;
}

[hidden] {
  display: none !important;
}

input[type="button"],
input[type="submit"],
input[type="reset"],
input[type="file"]::-webkit-file-upload-button,
button {
  border-radius: 0;
}

.icon-facebook {
  width: 0.53em;
  height: 1em;
  fill: #000;
}

/* Slider */
.slick-slider {
  position: relative;
  display: block;
  box-sizing: border-box;
  -moz-box-sizing: border-box;
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  -ms-touch-action: pan-y;
  touch-action: pan-y;
  -webkit-tap-highlight-color: transparent;
}

.slick-list {
  position: relative;
  overflow: hidden;
  display: block;
  margin: 0;
  padding: 0;
}

.slick-list:focus {
  outline: none;
}

.slick-list.dragging {
  cursor: pointer;
  cursor: hand;
}

.slick-slider .slick-track,
.slick-slider .slick-list {
  -webkit-transform: translate3d(0, 0, 0);
  -moz-transform: translate3d(0, 0, 0);
  -ms-transform: translate3d(0, 0, 0);
  -o-transform: translate3d(0, 0, 0);
  transform: translate3d(0, 0, 0);
}

.slick-track {
  position: relative;
  left: 0;
  top: 0;
  display: block;
}

.slick-track:before, .slick-track:after {
  content: "";
  display: table;
}

.slick-track:after {
  clear: both;
}

.slick-loading .slick-track {
  visibility: hidden;
}

.slick-slide {
  float: left;
  height: 100%;
  min-height: 1px;
  display: none;
}

[dir="rtl"] .slick-slide {
  float: right;
}

.slick-slide img {
  display: block;
}

.slick-slide.slick-loading img {
  display: none;
}

.slick-slide.dragging img {
  pointer-events: none;
}

.slick-initialized .slick-slide {
  display: block;
}

.slick-loading .slick-slide {
  visibility: hidden;
}

.slick-vertical .slick-slide {
  display: block;
  height: auto;
  border: 1px solid transparent;
}

.slick-slide > div {
  height: 100%;
}

html,
body {
  height: 100%;
  overflow: hidden;
}

body {
  min-height: 570px;
  color: #333;
  background: #f8f8f8;
  font-family: 'Roboto Light', Arial, sans-serif;
  font-size: 14px;
  line-height: 1.2;
}

*::-webkit-scrollbar {
  display: none !important;
}

.body-darkgray {
  background: #eee;
}

hr {
  margin: 0;
  background: rgba(0, 0, 0, 0.1);
  color: rgba(0, 0, 0, 0.1);
  border: none;
  height: 2px;
  width: 100%;
  clear: both;
}

.overlay {
  position: fixed;
  z-index: 100;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 100%;
  opacity: 0;
  visibility: hidden;
  background: #000;
  cursor: default;
  transition: opacity 0.2s ease-out, visibility 0.2s ease-out;
}

.overlay[data-color="dark-gray"] {
  background: #434c55;
}

.overlay.is-open {
  opacity: 0.8;
  visibility: visible;
}

.form-wrapper {
  padding: 36px 0 20px;
  margin: 0 auto;
  max-width: 460px;
}

.loading-page__icon {
  margin: 80px 0 60px;
}

.box {
  margin: 0 auto;
  width: 100%;
  max-width: 450px;
  *zoom: 1;
}

.box:after {
  content: " ";
  display: table;
  clear: both;
}

.box__footnote {
  margin-top: 30px;
}

.box__header {
  margin-bottom: 20px;
}

.box__meta {
  margin: 30px 0;
}

.page-activate .intro {
  margin-top: 50px;
  margin-bottom: 28px;
}

.footnote {
  color: #aaa;
  font-family: 'Roboto Bold', Arial, sans-serif;
  font-size: 14px;
  text-align: center;
}

.footnote_lg {
  font-size: 16px;
}

.footnote_light {
  font-family: 'Roboto Regular', Arial, sans-serif;
}

.footnote a {
  color: #2e99db;
}

.no-touch .footnote a:hover {
  text-decoration: underline;
}

.footnote a:active {
  color: #2183bf;
}

.no-touch .footnote a:active {
  color: #2183bf;
}

.loader {
  position: absolute;
  z-index: 3000;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(255, 255, 255, 0.9);
  text-align: center;
  font-size: 0;
}

.loader:before {
  content: "";
  display: inline-block;
  vertical-align: middle;
  width: 0;
  height: 100%;
}

.loader_fullscreen {
  position: fixed;
}

.loader .loader__icon {
  display: inline-block;
  vertical-align: middle;
}

.loader__center {
  display: inline-block;
  vertical-align: middle;
  width: 100%;
}

.loader__center .loader__icon {
  vertical-align: top;
}

.loader__text {
  margin: 0 auto;
  max-width: 400px;
  color: #333;
  font-size: 16px;
}

.wrapper-btn-center {
  margin: 0 auto;
  max-width: 300px;
}

.wrapper-common {
  padding: 0 20px 10px;
}

.step-body {
  position: relative;
  padding-bottom: 100px;
}

.step-body__section {
  overflow: auto;
  padding: 20px 0 0;
  height: 100%;
}

.step-body__footer {
  position: absolute;
  left: 0;
  right: 0;
  bottom: 0;
}

.explanation__icon {
  float: left;
}

.explanation__text {
  overflow: hidden;
  padding: 10px 0;
}

.explanation__text p {
  margin: 0;
}

.explanation__error {
  clear: both;
}

.signature-box {
  position: relative;
  margin-bottom: 20px;
  border-top: 1px solid #dadada;
  border-bottom: 1px solid #dadada;
  min-height: 250px;
  min-height: 300px;
  min-height: calc(230px + (300 - 230) * ((100vh - 570px) / (650 - 570)));
}

@media (max-height: 570px) {
  .signature-box {
    min-height: 230px;
  }
}

@media (min-height: 650px) {
  .signature-box {
    min-height: 300px;
  }
}

.is-poynt .signature-box {
  min-height: 450px;
}

.signature-box__close {
  position: absolute;
  top: 10px;
  right: 10px;
}

.vertical-center {
  text-align: center;
  font-size: 0;
}

.vertical-center:before {
  content: "";
  display: inline-block;
  vertical-align: middle;
  width: 0;
  height: 100%;
}

.vertical-center__content {
  display: inline-block;
  vertical-align: middle;
  width: 100%;
  text-align: left;
}

.is-hidden {
  display: none;
}

.is-dropdown-upper {
  z-index: 100 !important;
}

[data-animation*="ripple"] {
  position: relative;
  overflow: hidden;
}

[data-animation*="ripple"] .ripple-circle {
  position: absolute;
  z-index: 20;
  top: 50%;
  left: 50%;
  border-radius: 50%;
  width: 112px;
  height: 112px;
  background: rgba(0, 0, 0, 0.1);
  animation: ripple 0.5s linear forwards;
  pointer-events: none;
}

@keyframes ripple {
  0% {
    opacity: 1;
    transform: scale(0);
  }
  100% {
    opacity: 0;
    transform: scale(2);
  }
}

.version {
  position: fixed;
  bottom: 20px;
  right: 20px;
  font-size: 14px;
  white-space: nowrap;
}

.hide {
  display: none !important;
}

.w100 {
  width: 100%;
}

.w50 {
  width: 50%;
}

.h100 {
  height: 100%;
}

.cutout-left {
  box-shadow: inset 6px 0 4px -5px rgba(0, 0, 0, 0.2);
}

.cutout-right {
  box-shadow: inset -6px 0 4px -5px rgba(0, 0, 0, 0.2);
}

.cutout-bottom {
  box-shadow: inset 0 -6px 4px -5px rgba(0, 0, 0, 0.2);
}

.cutout-top {
  box-shadow: inset 0 6px 4px -5px rgba(0, 0, 0, 0.2);
}

.baseline {
  vertical-align: middle !important;
}

.clearfix {
  *zoom: 1;
}

.clearfix:after {
  content: " ";
  display: table;
  clear: both;
}

.no-margin {
  margin: 0 !important;
}

.no-padding {
  padding: 0 !important;
}

.no-vpadding {
  padding-top: 0 !important;
  padding-bottom: 0 !important;
}

.padding-top {
  padding-top: 10px;
}

.padding-left {
  padding-left: 10px;
}

.padding-right {
  padding-right: 10px;
}

.padding2-left {
  padding-left: 20px;
}

.padding2-right {
  padding-right: 20px;
}

.no-scrollbar::-webkit-scrollbar {
  display: none;
}

.valign-middle {
  vertical-align: middle !important;
}

.valign-top {
  vertical-align: top !important;
}

.valign-bottom {
  vertical-align: bottom !important;
}

.text-overflow {
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}

.no-wrap {
  white-space: nowrap;
}

.grid {
  *zoom: 1;
}

.grid:after {
  content: " ";
  display: table;
  clear: both;
}

.grid-2-col .grid__item {
  float: left;
  width: 50%;
}

.grid-3-col .grid__item {
  float: left;
  width: 33.33333%;
}

.grid-4-col .grid__item {
  float: left;
  width: 25%;
}

.grid-normal-spacing {
  margin-right: -10px;
}

.grid-normal-spacing .grid__item {
  padding: 0 10px 10px 0;
}

.grid-double-spacing {
  margin-right: -20px;
}

.grid-double-spacing .grid__item {
  padding: 0 20px 20px 0;
}

